
import { defineComponent, ref } from 'vue';
import Toggle from '@vueform/toggle';
import { QuickFetch, QuickData } from '@/quick';
import SmallModal from '@/components/SmallModal.vue';

export default defineComponent({
  setup(props, context) {
    // quick
    const qf = new QuickFetch();
    const qd = new QuickData();
    // settings
    const displayModal = ref(0);
    // lists
    const sportList: any = ref([]);
    function getSport() {
      qf.Url('sports/list').GetAll(sportList);
    }
    getSport();
    const divisionList: any = ref([]);
    function getDivision() {
      qf.Url('division/list').GetAll(divisionList);
    }
    getDivision();
    // data
    const nameCh = ref('');
    const nameEn = ref('');
    const info = ref('');
    const type = ref('athl');
    const hostDivision: any = ref([]);
    const regStart = ref('');
    const regEnd = ref('');
    const eventStart = ref('');
    const nsysu = ref(0);
    const nsysuGame = ref(0);
    const regSwitch = ref(1);
    const sameOrg = ref(0);
    const sameDept = ref(0);
    const onlyTeam = ref(0);
    const regUnit = ref(0);
    const useReg = ref(1);
    const regUrl = ref('');
    const useManage = ref(0);
    const manageUrl = ref('');
    const useSite = ref(1);
    const siteUrl = ref('');
    // submit
    function submitAll() {
      if (useReg.value === 0) {
        regUrl.value = '';
      }
      if (useManage.value === 0) {
        manageUrl.value = '';
      }
      if (useSite.value === 0) {
        siteUrl.value = '';
      }
      qf.Dataset()
        .AddObj('name_ch', nameCh)
        .AddObj('name_en', nameEn)
        .AddObj('info', info)
        .AddObj('type', type)
        .AddObj('reg_start', regStart)
        .AddObj('reg_end', regEnd)
        .AddObj('event_start', eventStart)
        .AddObj('nsysu', nsysu)
        .AddObj('nsysu_game', nsysuGame)
        .AddObj('reg_switch', regSwitch)
        .Add('host_division', JSON.stringify(hostDivision.value))
        .AddObj('reg_unit', regUnit)
        .AddObj('same_org', sameOrg)
        .AddObj('same_dept', sameDept)
        .AddObj('only_team', onlyTeam)
        .AddObj('use_reg', useReg)
        .AddObj('reg_url', regUrl)
        .AddObj('use_manage', useManage)
        .AddObj('manage_url', manageUrl)
        .AddObj('use_site', useSite)
        .AddObj('site_url', siteUrl);
      qf.Url('games/add').Post(qf.GetDset()).then((res: any) => {
        if (res.message === 'done') {
          qd.Alert('已新增比賽');
          context.emit('refresh');
          context.emit('close_modal');
        }
      });
    }
    return {
      nameCh,
      nameEn,
      info,
      type,
      hostDivision,
      regStart,
      regEnd,
      eventStart,
      nsysu,
      nsysuGame,
      regSwitch,
      regUnit,
      sameOrg,
      sameDept,
      onlyTeam,
      useReg,
      regUrl,
      useManage,
      manageUrl,
      useSite,
      siteUrl,
      submitAll,
      sportList,
      divisionList,
      displayModal,
    };
  },
  components: {
    Toggle,
    SmallModal,
  },
});
