
import { defineComponent, ref, reactive } from 'vue';
import SmallModal from '@/components/SmallModal.vue';
import { QuickData, QuickFetch } from '@/quick';
import AddGame from '@/components/home/AddGame.vue';
import { useRouter } from 'vue-router';
import { useStore } from 'vuex';

export default defineComponent({
  setup() {
    const displayModal = ref(0);
    const qf = new QuickFetch();
    const qd = new QuickData();
    const router = useRouter();
    const store = useStore();
    const sports: any = reactive([]);
    const division: any = ref(0);
    const userData: any = reactive(JSON.parse(localStorage.sep5_admin_data));
    function getSports() {
      qf.Url('sports/list').Get().then((res: any) => {
        res.forEach((sport: any) => {
          if (JSON.parse(sport.division).includes(userData.division_id)) {
            sports.push(sport.code);
          }
        });
      });
    }
    getSports();
    const gameList: any = ref([]);
    function getGames() {
      qf.Url('games/list').GetAll(gameList);
    }
    getGames();
    const divisionList: any = ref([]);
    function getDivision() {
      qf.Url('division/list').GetAll(divisionList).then(() => {
        divisionList.value.forEach((d: any) => {
          if (d.division_id === userData.division_id) {
            division.value = d;
          }
        });
      });
    }
    getDivision();
    function openGame(gameId: number, type: string) {
      store.commit('setGameId', gameId);
      if (type === 'athl') {
        router.push(`/athletics/${gameId}`);
      } else {
        router.push(`/general/${type}/${gameId}`);
      }
    }
    return {
      displayModal,
      getGames,
      gameList,
      divisionList,
      openGame,
      userData,
      sports,
      division,
    };
  },
  components: {
    SmallModal,
    AddGame,
  },
});
